import * as React from "react"
import { Link, graphql } from "gatsby"
import moment from "moment"
import "github-markdown-css/github-markdown-light.css";
import { getSrc } from "gatsby-plugin-image"

import "katex/dist/katex.min.css"
import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"

const BlogPostTemplate = ({
  data: { previous, next, site, markdownRemark: post },
  location,
}) => {
  const siteTitle = site.siteMetadata?.title || `Title`

  return (
    <Layout location={location} title={siteTitle}>
		<header>
			<section>
				<h1 itemProp="headline">{post.frontmatter.title}</h1>
				<p className="p">{moment(post.frontmatter.date).format(`YYYY年MM月DD日 HH:mm`)}</p>
			</section>
		</header>
		<main>
			<article
				// className="blog-post"
				className="markdown-body"
				itemScope
				itemType="http://schema.org/Article"
			>
				<section
				dangerouslySetInnerHTML={{ __html: post.html }}
				itemProp="articleBody"
				/>
				<hr />
				<nav className="blog-post-nav">
					<ul
					style={{
						display: `flex`,
						flexWrap: `wrap`,
						justifyContent: `space-between`,
						listStyle: `none`,
						padding: 0,
					}}
					>
					<li>
						{previous && (
						<Link to={previous.fields.slug} rel="prev">
							<p className="blog-post-nav-arrow">← {previous.frontmatter.title}</p>
						</Link>
						)}
					</li>
					<li>
						{next && (
						<Link to={next.fields.slug} rel="next">
							<p className="blog-post-nav-arrow">{next.frontmatter.title} →</p>
						</Link>
						)}
					</li>
					</ul>
				</nav>
			</article>
			<Bio />
		</main>
    </Layout>
  )
}

export const Head = ({ data: { markdownRemark: post } }) => {
	const imageSrc = getSrc(post.frontmatter.image)

  return (
    <Seo
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
	  image={imageSrc}
    />
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date
        description
		tags
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`
